hr {
  position: relative;
  top: -25px;
  border: 0;
  height: 1px;
  width: 40%;
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    #767676,
    rgba(0, 0, 0, 0)
  );
}

.social {
  color: #767676;
  margin: -1px;
}
@media (min-width: 601px) {
    .social {
  color: #767676;
  margin: -1px;
}
}
/* If the screen size is 600px wide or less, set the font-size of <div> to 30px */
@media screen and (max-width: 600px) {
    .social {
  color: #767676;
  margin: -5px;
}
}

.social:hover {
  color: #eb655b;
  transform: scale(1.3);
  transition: transform 0.2s;
}

footer.footer {
  padding-top: 5rem;
  padding-bottom: 5rem;
  font-size: 20px;
}
